import React, { useState } from "react";
import { Line } from "react-chartjs-2";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Tooltip,
    Legend,
} from "chart.js";

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Tooltip,
    Legend
);

const questions = [
    "    ?",
    "      ?",
    "    ?",
    "    ?",
    "       ?",
    "         ?",
    "       ?",
    "         ?",
    "     ?",
    "           ?",
    "      ?",
    "      ?",
    "       7 ?",
    "     ?",
    "     ?",
    "     ?",
    "      ?",
    "           ?",
    "       ?",
    "       ?"
];

const adviceByScore = (score) => {
    if (score <= 40) {
        return {
            title: "   !",
            text:
                " ,             .",
            challenge: ":  3   3     /.",
            color: "#d9534f", // 
        };
    } else if (score <= 70) {
        return {
            title: " ,    !",
            text: "           .",
            challenge: ":  5     .",
            color: "#f0ad4e", // 
        };
    } else {
        return {
            title: "  !",
            text:
                "           .",
            challenge: ":       .",
            color: "#5cb85c", // 
        };
    }
};

const MoodTracker = () => {
    const [answers, setAnswers] = useState([]);
    const [submitted, setSubmitted] = useState(false);

    const handleChange = (index, value) => {
        const newAnswers = [...answers];
        newAnswers[index] = Number(value);
        setAnswers(newAnswers);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        setSubmitted(true);
    };

    const totalScore = answers.reduce((a, b) => a + b, 0);
    const advice = adviceByScore(totalScore);

    // Progress bar calculation
    const answeredCount = answers.filter((a) => a !== null).length;
    const progressPercent = (answeredCount / questions.length) * 100;

    const isDisabled = () => {
        return answers.length < questions.length || answers.includes(undefined) || answers.includes(null);
    }

    // Chart data and options
    const chartData = {
        labels: questions.map((_, i) => ` ${i + 1}`),
        datasets: [
            {
                label: " (1-5)",
                data: answers,
                fill: false,
                backgroundColor: "#00B9F2",
                borderColor: "#00B9F2",
                tension: 0.3,
                pointHoverRadius: 9,
                pointRadius: 6,
                borderWidth: 3,
                hoverBorderWidth: 4,
            },
        ],
    };

    const chartOptions = {
        responsive: true,
        plugins: {
            legend: { display: false },
            tooltip: {
                enabled: true,
                backgroundColor: '#00B9F2',
                titleFont: { size: 16, weight: 'bold' },
                bodyFont: { size: 14 },
                cornerRadius: 6,
                padding: 10,
            },
        },
        scales: {
            y: {
                min: 1,
                max: 5,
                ticks: { stepSize: 1, color: '#00B9F2', font: { weight: '600' } },
                grid: { color: '#f2f2f2' },
                title: {
                    display: true,
                    text: "",
                    color: '#00B9F2',
                    font: { size: 14, weight: '600' },
                },
            },
            x: {
                ticks: { color: '#00B9F2', font: { weight: '600' } },
                grid: { display: false },
                title: {
                    display: true,
                    text: "",
                    color: '#00B9F2',
                    font: { size: 14, weight: '600' },
                },
            },
        },
    };

    return (
        <div
            className="container my-5"
            style={{
                fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
                color: "#3a2f2f",
                maxWidth: "900px",
            }}
        >
            <h2 className="mb-4" style={{ color: "#00B9F2", textAlign: "center", fontWeight: "700", fontSize: "2.75rem", letterSpacing: "2px", textShadow: "1px 1px 2px #ccc" }}>
                Mood Tracker
            </h2>

            {!submitted ? (
                <>
                    <div className="mb-4" style={{ height: '30px', borderRadius: '15px', backgroundColor: '#f0e6d2', overflow: 'hidden' }}>
                        <div
                            className="progress-bar"
                            role="progressbar"
                            style={{
                                width: `${progressPercent}%`,
                                backgroundColor: "#00B9F2",
                                height: '100%',
                                borderRadius: '15px',
                                transition: "width 0.7s ease-in-out",
                                boxShadow: '0 0 8px #00B9F2',
                            }}
                            aria-valuenow={progressPercent}
                            aria-valuemin="0"
                            aria-valuemax="100"
                        />
                    </div>
                    <p style={{ textAlign: 'right', fontWeight: '600', marginTop: '5px', color: '#00B9F2' }}>
                        {Math.round(progressPercent)}% 
                    </p>
                    <div className="d-flex justify-content-between align-items-center mb-4 px-3 py-2 rounded" style={{ backgroundColor: '#f2f9fb', border: '1px dashed #00B9F2' }}>
                        <span> 1 - </span>
                        <span> 2 - </span>
                        <span> 3 - </span>
                        <span> 4 - </span>
                        <span> 5 - </span>
                    </div>
                    <form onSubmit={handleSubmit} className="m-4">
                        {questions.map((q, i) => (
                            <div
                                className="mb-4"
                                key={i}
                                style={{
                                    backgroundColor: "#f9f6f1",
                                    padding: "15px",
                                    borderRadius: "10px",
                                    boxShadow: "0 2px 6px rgba(0, 185, 242, 0.15)",
                                    transition: "transform 0.3s",
                                }}
                                onMouseEnter={e => e.currentTarget.style.transform = "scale(1.02)"}
                                onMouseLeave={e => e.currentTarget.style.transform = "scale(1)"}
                            >
                                <label
                                    className="form-label"
                                    style={{ fontWeight: "600", color: "#00B9F2", fontSize: "1.1rem" }}
                                    htmlFor={`question-${i}`}
                                >
                                    {q}
                                </label>
                                <select
                                    className="form-select shadow-sm rounded-3 px-3 py-2"
                                    id={`question-${i}`}
                                    value={answers[i]}
                                    onChange={(e) => handleChange(i, e.target.value)}
                                    style={{
                                        border: "2px solid #00B9F2",
                                        fontWeight: "600",
                                        backgroundColor: "#dde7e8",
                                        color: "#000000",
                                        transition: "0.3s ease-in-out",
                                    }}
                                >
                                    <option value={0}> </option>
                                    <option value={1}>1</option>
                                    <option value={2}>2</option>
                                    <option value={3}>3</option>
                                    <option value={4}>4</option>
                                    <option value={5}>5</option>
                                </select>
                            </div>
                        ))}
                        <button
                            type="submit"
                            className="btn btn-primary mt-3"
                            disabled= {isDisabled()}
                            style={{
                                backgroundColor: "#00B9F2",
                                borderColor: "#00B9F2",
                                fontWeight: "700",
                                fontSize: "1.1rem",
                                padding: "10px 20px",
                                boxShadow: "0 4px 8px rgba(0, 185, 242, 0.4)",
                                transition: "background-color 0.3s ease",
                            }}
                            onMouseEnter={e => e.currentTarget.style.backgroundColor = "#008ecf"}
                            onMouseLeave={e => e.currentTarget.style.backgroundColor = "#00B9F2"}
                        >
                            {isDisabled() ? "   " : ""}
                        </button>
                    </form>
                </>
            ) : (
                <div
                    className="p-4 rounded-4"
                    style={{
                        backgroundColor: "#FFFADA",
                        border: `3px solid ${advice.color}`,
                        boxShadow: `0 0 20px ${advice.color}`,
                        textAlign: "center",
                        maxWidth: "700px",
                        margin: "auto",
                    }}
                >
                    <h3 style={{ color: advice.color, fontWeight: "800", marginBottom: "10px" }}>
                        {advice.title}
                    </h3>
                    <p style={{ fontSize: "1.25rem", fontWeight: "600" }}>{advice.text}</p>
                    <p style={{ fontStyle: "italic", marginTop: "20px", fontWeight: "600", color: advice.color }}>
                        {advice.challenge}
                    </p>
                    <p
                        style={{
                            fontWeight: "700",
                            fontSize: "1rem",
                            marginTop: "30px",
                            color: "#28231D",
                            userSelect: "none",
                        }}
                    >
                         : <span style={{ color: advice.color }}>{totalScore} </span>
                    </p>

                    <div style={{ marginTop: "30px" }}>
                        <Line data={chartData} options={chartOptions} />
                    </div>
                    <button
                        className="btn btn-outline-secondary mt-4"
                        onClick={() => {
                            setAnswers([]);
                            setSubmitted(false);
                        }}
                        style={{
                            borderColor: "#00B9F2",
                            color: "#00B9F2",
                            fontWeight: "600",
                            transition: "0.3s",
                        }}
                        onMouseEnter={e => {
                            e.currentTarget.style.backgroundColor = "#00B9F2";
                            e.currentTarget.style.color = "#FFFADA";
                        }}
                        onMouseLeave={e => {
                            e.currentTarget.style.backgroundColor = "transparent";
                            e.currentTarget.style.color = "#00B9F2";
                        }}
                    >
                         
                    </button>
                </div>
            )}
        </div>
    );
};

export default MoodTracker;