import React from 'react';
import { Bar } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Tooltip,
    Legend,
    Title
} from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, BarElement, Tooltip, Legend, Title);

const HBSCGenderChart = () => {
    const data = {
        labels: [' ', '  ', '  '],
        datasets: [
            {
                label: '',
                data: [25, 18, 30],
                backgroundColor: '#74b9ff',
            },
            {
                label: '',
                data: [43, 36, 51],
                backgroundColor: '#ff7675',
            },
        ],
    };

    const options = {
        responsive: true,
        plugins: {
            title: {
                display: true,
                text: '    (HBSC)',
                font: {
                    size: 18,
                },
            },
            legend: {
                position: 'top',
            },
            tooltip: {
                enabled: true,
                callbacks: {
                    label: function (context) {
                        const label = context.dataset.label || '';
                        const value = context.raw;
                        const category = context.label;

                        let description = '';

                        if (category === ' ') {
                            description = '      ';
                        } else if (category === '  ') {
                            description = '     ';
                        } else if (category === '  ') {
                            description = '      ';
                        }

                        return `${label}: ${value}%  ${description}`;
                    },
                },
            },
        },
        scales: {
            y: {
                beginAtZero: true,
                max: 100,
                ticks: {
                    stepSize: 10,
                },
            },
        },
    };


    return <Bar data={data} options={options} />;
};

export default HBSCGenderChart;
